<template>
  <div id="app">
    <router-view v-bind:user="user" @change="changeUser"/>
  </div>
</template>
<script>

  import NavMenu from '@/components/common/NavMenu'

  export default {
    name: 'App',
    components: {NavMenu},
    data () {
      return {
        user: {
          uid: '',
          mail: '',
          name: ''
        }
      }
    },
    provide () {     // provide是一个匿名函数，返回一个对象
      return {
        user: this.user
      }
    },
    methods: {
      changeUser (user) {
        this.user=user
      }
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    /*margin-top: 10px;*/
  }
</style>



